* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

ul,ol {
  list-style: none;
}

.btn {
  border: none;
  background: transparent;
}

.app-input {
  border: none;
  box-shadow: none;
}

body {
  min-height: 100vh;
  font-family: sans-serif;
  font-size: 14px;
  color: #9b9b9b;
  font-weight: 400;
  line-height: 1.4;
}

.app-header {
  background-color: crimson;
  color: white;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.app-title {
  font-size: 18px;
  text-transform: uppercase;
}

.app-main-content {
  padding: 20px;
  display: flex;
}

.app-searcher-favourites {
  width: 40%;
}

.app-searcher {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  padding-bottom: 20px;
  border-bottom: 2px solid crimson;
}

.app-label {
  color: #9b9b9b;
  margin-bottom: 10px;
}

.app-input {
  height: 30px;
  padding-left: 5px;
  margin-bottom: 10px;
  border-bottom: 1px solid crimson;
}

.btn {
  background-color: crimson;
  color: white;
  padding: 2px 10px;
  margin-left: 5px;
  margin-bottom: 10px;
}

.favourites {
  margin-top: 20px;
}

.favourites__title {
  color: crimson;
  font-size: 16px;
}

.preview--favourite {
  width: 100px;
}

.preview--favourite img {
  width: 100%;
}

.preview--favourite h3 {
  font-size: 12px;
}

.results {
  margin-left: 20px;
  padding-left: 20px;
  border-left: 2px solid crimson;
}

.results__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.results__title {
  margin-bottom: 10px;
  color: crimson;
  font-size: 16px;
  text-align: center;
}

.show-card {
  color: blue;
  background-color: crimson;
  padding: 10px;
  margin: 5px;
}

.show-card--favourite {
  color: crimson;
  background-color: blue;
}

.app-result {
  display: flex;
}

.app-footer {
  background-color: yellow;
}

@media all and (min-width: 1200px) {
  .page-container {
    max-width: 1200px;
    margin: 0 auto;
  }
}
